<template>
  <div>
    <div  style="margin-left:50px;">
      <h3>发票信息</h3>
    </div>
    <div>
    	
      <el-form
        class="mt15"
        :status-icon="true"
        :model="form"
        :rules="rules"
        ref="form"
        label-width="120px"
        label-position="right"
      >
        <el-form-item label="电子邮箱" prop="email">
            <el-col :span="8">
                <el-input v-model="form.email" :disabled="isShow"></el-input>
            </el-col>
            <el-col :span="8" style="padding-left:10px; color:red;">(用于电子发票，将会发送到这个邮箱)</el-col>
        </el-form-item>
       
        <el-form-item label="详细地址" prop="courier_address">
            <el-col :span="8">
                <el-input v-model="form.courier_address" :show-word-limit="true" :disabled="isShow"></el-input>
            </el-col>
            <el-col :span="8" style="padding-left:10px; color:red;">( 用于快递发票 邮寄地址)</el-col>
        </el-form-item>
        <el-form-item label="联系人" prop="contact">
            <el-col :span="8">
                <el-input v-model="form.contact"></el-input>
            </el-col>
        </el-form-item>
        <el-form-item label="联系电话" prop="contact_phone">
            <el-col :span="8">
                <el-input v-model="form.contact_phone" ></el-input>
            </el-col>
        </el-form-item>
        
        <el-form-item>
          <el-button type="primary" @click="submitForm('form')">提交</el-button>
        </el-form-item>
      </el-form>
    </div>
  </div>
</template>

<script>
// import mavonEditor from 'mavon-editor'
// import 'mavon-editor/dist/css/index.css' 
export default {
  name: "address",
  data() {
    return {
      isShow:false,
      form: {
        email: "", //email
        contact:'', //联系人
        contact_phone: "", //联系人电话
        courier_address: "", //地址
      },
      rules: {
        courier_address: [
          { required: true, message: "请输入联系地址", trigger: "blur" },
        ],
        contact: [
          { required: true, message: "请输入联系人", trigger: "blur" },
        ],
        contact_phone : [
            { required : true, message : '请联系人电话', trigger : "blur"}
        ]
      }
    };
  },

  created () {

    this.getInfo();
  },
  methods: {
  	getInfo()
  	{
  		this.$api.get('/enterprise/get_enterprise_info',{}, res => {
  			this.form=res.data.data
  		});
  	},
    submitForm(formName) {
    	var self = this;
      this.$refs[formName].validate(valid => {
        if (valid) {
          this.$api.post('enterprise/edit_enterprise_info', this.form, res=>{
            if(res.data.bol)
            {
              this.$message.success(res.data.msg);
              setTimeout(() => {
               	self.getInfo();
            }, 1500);
            }
          });
        } else {
          console.log("error submit!!");
          return false;
        }
      });
    },
    resetForm(formName) {
      this.$refs[formName].resetFields();
    }
  }
};
</script>

<style>
body {
  background: #f1f1f1;
}
	.succ , .error, .runing, .wei{color:#fff; padding: 5px 5px 5px 20px; border-radius: 5px;}
  .succ{background:#67C23A;}
  .error{background:#F56C6C}
  .runing{background:#909399}
  .wei{background:#E6A23C;}
</style>



